.page {
  font-family: Arial, sans-serif;
}

.body {
  place-content: center;
  display: flex;
}

.main {
  grid-template-columns: repeat(2, 200px);
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.section {
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.section__photo {
  border-radius: 50%;
  width: 100%;
  transition: border-radius 1s;
  position: relative;
}

.section__photo:hover {
  border-radius: 20px;
}

.section__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  line-height: 32px;
  position: absolute;
}

.section__details {
  text-align: center;
}

.details__name {
  text-align: center;
  color: #2f4f4f;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
}

.extra__sport {
  text-align: center;
  font-size: 18px;
  line-height: 120%;
}

.extra__sport:after {
  content: ",";
}

.extra__pay:after {
  content: "M";
}

.extra__pay:before {
  content: "$";
}

.details__extra {
  visibility: hidden;
}

.person {
  padding: 10px;
  transition: transform 1s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .details__extra {
  visibility: visible;
}
